/* Brand Blue */
:export {
  light_chart_grid: rgba(0, 0, 0, 0.04);
  dark_chart_grid: #191c31;
  light_chart_mountain_border: #999cac;
  dark_chart_mountain_border: white;
  light_chart_mountain_bg: rgba(153, 156, 172, 0.16);
  dark_chart_mountain_bg: rgba(255, 255, 255, 0.36);
  light_chart_mountain_bg_shade: rgba(255, 255, 255, 0);
  dark_chart_mountain_bg_shade: rgba(16, 19, 32, 0);
  light_candle_text_closed: rgba(0, 0, 0, 0.16);
