        flex-direction: row;
        margin: 0 4px;
        align-items: center;
        font-weight: 300; }
.ciq-period .cq-menu-dropdown .interval .row span {
          cursor: pointer;
          margin: 0 3px 0 0;
          width: 24px;
          height: 24px;
          text-align: center;
          line-height: 26px;
          border: 1px solid transparent;
          border-radius: 4px; }
.ciq-period .cq-menu-dropdown .interval .row span.selected {
