.ciq-list {
  position: relative; }
.ciq-list.ps {
    padding-right: 11px; }
.ciq-list.scrollbar-container .ciq-list-item:first-child {
    margin-top: 16px; }
.ciq-list.scrollbar-container .ciq-list-item:nth-last-child(-n+3) {
    margin-bottom: 16px; }
.ciq-list .ciq-list-item {
    cursor: pointer;
    line-height: normal;
    font-size: 1.2em;
    height: auto;
    padding: 8px 16px;
